import React from 'react';
import styled from 'styled-components';
import Fade from 'react-reveal/Fade';

const Container = styled.div`
    width: 100%;
    min-height: 100vh;
`

const ContainerTrabajos = styled.div`
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

`

const Trabajo = styled.div`
    width: 300px;
    height: 230px;
    background: #4EE342;
    margin: 20px;
`

const Toolbar = styled.div`
    width: 100%;
    height: 100px;
`

const Titulo = styled.h1`

`

const Clientes = () => {
    return ( 
        <Container>
            <Fade top>
                <Titulo>TRABAJOS DESTACADOS</Titulo>
            </Fade>
            <ContainerTrabajos>
                <Fade>
                <Trabajo />
                </Fade>
                <Fade>
                <Trabajo />
                </Fade>
                <Fade>
                <Trabajo />
                </Fade>
                <Fade>
                <Trabajo />
                </Fade>
                <Fade>
                <Trabajo />
                </Fade>
                <Fade>
                <Trabajo />
                </Fade>
                <Fade>
                <Trabajo />
                </Fade>
                <Fade>
                <Trabajo />
                </Fade>
                <Fade>
                <Trabajo />
                </Fade>
            </ContainerTrabajos>
        </Container>
    );
}
 
export default Clientes;